/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Global fix */
html, body {
  height: auto;
  min-height: 100%;
  overflow-y: auto;
  scroll-behavior: smooth;
  font-family: 'Segoe UI', sans-serif;
  background: #ffffff;
}

.home-container {
    padding-left: 5%;
    padding-right: 5%;
    justify-self: center;
  width: 90%;
  overflow-x: hidden;
  color: #001f3f !important;
}



/* Navbar */
.navbar-left {
  width: 50%;
  display: flex;
  align-items: center;
}

.navbar-logo {
  height: 200px;
  cursor: pointer;
}
.navbar-links-container {
  position: fixed;
  top: 0;
  right: -50%;
  width: 50%;
  height: 100vh;
  background: #fff;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.1);
  transition: right 0.3s ease-in-out;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar-links-container.open {
  right: 0;
}

.navbar-menu {
  display: none;
}

.navbar-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.navbar-links li a {
  text-decoration: none;
  color: #002b7f;
  font-weight: 600;
  font-size: 18px;
}

.navbar-desktop-links {
  display: flex;
  gap: 24px;
  list-style: none;
}

.navbar-desktop-links li a {
  text-decoration: none;
  color: #002b7f;
  font-weight: 600;
}

.close-icon {
  cursor: pointer;
  color: #002b7f;
}


/* Navbar */
.navbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 60px;
  background: #f9f9f9;
  z-index: 10;
  overflow: hidden;
}

/* Logo inside Navbar */
.navbar-logo img {
  height: 200px; 
  width: auto;
  object-fit: contain;
}

.canada-logo {
  height: 1em;           /* Match font size */
  vertical-align: middle;
  margin-right: 6px;     /* Optional space after image */
}

/* Hero Section */
.hero {
  position: relative;
  padding: 100px 60px 60px; /* 100px top padding = navbar height + spacing */
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff !important;
}

.hero-right {
  flex: 1;
  position: relative;
  z-index: 1;
}

.hero-image-placeholder {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  overflow: visible; /* allow overflow into left */
}

.hero-image-placeholder img.hero-image {
  max-width: 500px;  /* Adjust as needed */
  height: auto;
  object-fit: contain;
  transform: translateX(-80px); /* Shift image left to underlap */
  z-index: 1;
}


/* Hero Section */
/* .hero-left {
  flex: 1;
  min-width: 320px;
  padding-right: 40px;
} */

.hero-left {
  flex: 1;
  min-width: 320px;
  padding-right: 40px;
  position: relative; /* Needed if you add z-index */
  z-index: 2;
}

.hero-left h1 {
  font-size: 44px;
  font-weight: 800;
  margin-bottom: 20px;
  line-height: 1.3;
}

.hero-left h1 span {
  color: #002b7f;
}

.hero-left .ptag {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 30px;
  max-width: 520px;
}

.cta-button {
  background-color: #002b7f;
  color: white;
  padding: 14px 26px;
  border: none;
  font-size: 16px;
  font-weight: 600;
  border-radius: 8px;
  margin-bottom: 30px;
  cursor: pointer;
  transition: background 0.2s;
}

.cta-button:hover {
  background-color: #0040bf;
}

 .features-list {
  list-style: none; /* removes default bullets */
  padding-left: 0;  /* remove default left padding */
  font-size: 16px;
  line-height: 1.6;
}

.hero-right {
  flex: 1;
  min-width: 320px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
}

.hero-image-placeholder {
  width: 420px;
  height: auto;
  max-width: 100%;
  border-radius: 12px;
}

/* What is Section */
.what-is {
  background-color: #e6f0ff;
  padding: 80px 60px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.what-left {
  flex: 1;
  min-width: 300px;
  margin-right: 30px;
}

.what-left h2 {
  font-size: 45px;
  font-weight: 700;
  margin-bottom: 15px;
  color: #002b7f;
}

.what-left .ptag{
    font-size: 20px;
}

.what-left ul {
  margin-top: 15px;
  list-style: disc;
  padding-left: 20px;
  line-height: 1.6;
}

.what-right {
  flex: 1;
  min-width: 300px;
}


.what-left ul,
.what-box ul {
  list-style-type: none; /* Remove default bullets */
  padding-left: 0;        /* Remove left padding */
  margin-left: 0;         /* Optional: remove left margin */
}

.what-left ul li,
.what-box ul li {
  position: relative;
  font-size: 20px;
  padding-left: 24px; /* space for the ‚úì symbol */
}

/* No need for ::before since ‚úì is already in the text */


.what-box {
  background: white;
  padding: 28px;
  border-radius: 12px;
  box-shadow: 0 0 12px #ccc;
  font-size: 20px;
  line-height: 1.6;
}

/* Testimonials */
.testimonials {
  padding: 80px 60px;
  background-color: #ffffff;
  text-align: center;
}

.testimonials-title {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 40px;
  color: #002b7f;
}

.testimonial-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: center;
}

.testimonial-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 320px;
  flex: 1 1 280px;
}

/* Set fixed min-height so cards are same size */
.testimonial-card {
  background: #f0f7ff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 43, 127, 0.1);
  width: 100%;
  text-align: left;
  min-height: 150px;  /* adjust this value as needed */
  display: flex;
  align-items: center;
}

/* Quote styles */
.testimonial-quote {
  font-size: 18px;
  font-style: italic;
  line-height: 1.6;
  color: #001f3f;
  margin: 0;  /* remove default margin */
}

/* Wrap avatar and author in one flex row */
.testimonial-author-wrapper {
  display: flex;
  align-items: center;
  gap: 15px; /* space between image and name */
  margin-top: 15px;
  width: 100%;
  justify-content: center; /* center horizontally */
}

.testimonial-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  flex-shrink: 0;
}

.testimonial-author {
  font-size: 16px;
  font-weight: 600;
  color: #004080;
  white-space: nowrap; /* prevent name from breaking */
}


/* FAQ */

.faq {
  background: #e6f0ff;
  padding: 80px 60px 40px; /* less bottom padding for no big gap with footer */
}


.faq h2 {
  text-align: center;
  font-size: 40px;  /* Increased from 35px */
  color: #002b7f;
  margin-bottom: 40px;
}


/* Grid remains same, but increase max-width to allow bigger cards */
.faq-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px 50px; /* slightly bigger gaps */
  margin-top: 30px;
  max-width: 1000px;  /* Increased from 700px */
  margin-left: auto;
  margin-right: auto;
}
.faq-item {
  background: white;
  padding: 40px;  /* Increased from 24px */
  border-radius: 12px;
  box-shadow: 0 0 15px #bbb; /* slightly stronger shadow */
  font-size: 22px; /* increased text size */
  line-height: 1.5;
  font-weight: 600;
  color: #002b7f;
}

/* CTA button styling */
.faq-cta {
  margin-top: 50px;
  text-align: center;
}

.cta-button.full {
  width: 100%;
  max-width: 600px;  /* slightly wider */
  padding: 20px 0;   /* taller button */
  font-size: 20px;   /* bigger font */
  font-weight: 700;
  border-radius: 10px;
  cursor: pointer;
}

/* Footer with developer credit */
.footer-text {
  text-align: center;
  padding: 20px 0;
  background: white;
  font-weight: 500;
  color: #444;
  font-size: 14px;
  user-select: none;
}




/* Pricing Section */
.pricing-section {
  padding: 60px 0;
  background-color: #ffffff;
  text-align: center;
}

.pricing-title {
  font-size: 36px;
  color: #002b7f;
  margin-bottom: 40px;
}

.pricing-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 40px;
}

.pricing-card {
  background: #ffffff;
  border: 2px solid #dce3f2;
  border-radius: 12px;
  padding: 30px 20px;
  width: 280px;
  box-shadow: 0 4px 8px rgba(0, 43, 127, 0.05);
  text-align: left;
  transition: transform 0.2s;
}

.pricing-card:hover {
  transform: translateY(-4px);
}

.pricing-card h3 {
  font-size: 24px;
  color: #002b7f;
  margin-bottom: 20px;
}

.pricing-card p {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 10px;
}

.pricing-card.premium {
  border-color: #002b7f;
  background-color: #eaf0ff;
}







/* Responsive Styles */

/* Optional: Hide full nav links on larger screens if you later add them */
@media (min-width: 1025px) {
  .navbar-links-container {
    display: none !important;
  }
}

/* Large tablets and small desktops */
@media (max-width: 1024px) {
  .navbar {
    padding: 0 30px;
  }

  .navbar-menu {
    display: block;
    color: #002b7f;
    cursor: pointer;
  }

  .navbar-links-container {
    display: flex;
  }

  .navbar-desktop-links {
    display: none;
  }

  .hero {
    flex-direction: column;
    padding: 80px 30px 40px;
  }

  .hero-left, .hero-right {
    min-width: auto;
    padding: 0;
  }

  .hero-left h1 {
    font-size: 36px;
  }

  .hero-left p {
    font-size: 16px;
    max-width: 100%;
  }

  .hero-image-placeholder {
    width: 100%;
    max-width: 360px;
  }

  .what-is {
    padding: 60px 30px;
    flex-direction: column;
  }

  .what-left, .what-right {
    margin-right: 0;
    min-width: 100%;
    margin-bottom: 30px;
  }

  .what-left h2 {
    font-size: 36px;
  }

  .what-left p,
  .what-left ul li,
  .what-box {
    font-size: 18px;
  }

  .faq-grid {
    grid-template-columns: 1fr 1fr;
    max-width: 700px;
    grid-gap: 30px 30px;
  }

  .faq-item {
    font-size: 20px;
    padding: 30px;
  }
}

/* Tablets and larger phones */
@media (max-width: 768px) {
  .navbar {
    padding: 0 20px;
  }

  .navbar-logo {
    height: 140px;
  }

  .hero {
    padding: 60px 20px 40px;
    flex-direction: column;
  }

  .hero-left h1 {
    font-size: 28px;
    text-align: center;
  }

  .hero-left p {
    font-size: 16px;
    max-width: 100%;
    text-align: center;
  }

  .hero-right {
    padding-top: 20px;
  }

  .hero-image-placeholder img.hero-image {
    max-width: 450px;  /* Adjust as needed */
    height: auto;
    padding: 25px;
    object-fit: contain;
    transform: translateX(-80px); /* Shift image left to underlap */
    z-index: 1;
  }

  .hero-image-placeholder {
    width: 100%;
    max-width: 300px;
  }

  .what-is {
    padding: 40px 20px;
  }

  .what-left h2 {
    font-size: 28px;
    text-align: center;
  }

  .what-left p,
  .what-left ul li,
  .what-box {
    font-size: 16px;
  }

  .what-left,
  .what-right {
    min-width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .faq-grid {
    grid-template-columns: 1fr;
    max-width: 100%;
    grid-gap: 25px;
  }

  .faq-item {
    font-size: 18px;
    padding: 25px;
  }

  .cta-button.full {
    max-width: 100%;
    font-size: 18px;
    padding: 16px 0;
  }

  .testimonial-grid {
    flex-direction: column;
    gap: 30px;
  }

  .testimonial-item {
    max-width: 100%;
  }

  .testimonial-card {
    min-height: auto;
  }
}

/* Small phones */
@media (max-width: 480px) {
  .navbar-logo {
    height: 100px;
  }

  .hero-left h1 {
    font-size: 22px;
  }

  .hero-left p {
    font-size: 14px;
  }

  .what-left h2 {
    font-size: 22px;
  }

  .what-left p,
  .what-left ul li,
  .what-box {
    font-size: 14px;
  }

  .faq h2 {
    font-size: 28px;
  }

  .faq-item {
    font-size: 16px;
    padding: 20px;
  }

  .cta-button.full {
    font-size: 16px;
    padding: 14px 0;
  }

  .testimonial-quote {
    font-size: 16px;
  }

  .testimonial-avatar {
    width: 50px;
    height: 50px;
  }

  .testimonial-author {
    font-size: 14px;
  }
}
