/* Overlay */
.settings-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  padding: 10px;
}

/* Modal Container */
.settings-modal {
  display: flex;
  flex-direction: row;
  width: 100%;
  max-width: 800px;
  height: 90vh;
  background: var(--setting-content-main);
  /* background: #1e1e1e; */
  color: var(--chat-title);
  /* color: white; */
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.7);
}

/* Sidebar */
.settings-sidebar {
  width: 250px;
  background: var(--setting-sidebar-link);
  /* background: var(--setting-sidebar-link); */
  /* background: #2c2c2c; */
  padding: 20px;
  border-right: 1px solid #333;
  display: flex;
  flex-direction: column;
}

.settings-sidebar h2 {
  font-size: 1.5rem;
  margin-bottom: 24px;
  color: var(--setting-h2);
  /* color: #eee; */
}

.settings-sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-grow: 1;
}

.settings-sidebar li {
  display: flex;
  align-items: center;
  padding: 10px 12px;
  cursor: pointer;
  border-radius: 6px;
  color: var(--setting-li);
  /* color: #ccc; */
  font-size: 0.95rem;
  transition: background 0.2s, color 0.2s;
}

.settings-sidebar li svg {
  margin-right: 10px;
  font-size: 1.1rem;
}

.settings-sidebar li:hover,
.settings-sidebar li.active {
  background-color: var(--setting-li-hover);
  /* background-color: #3a3a3a; */
  color: #fff;
}

/* Content Area */
.settings-content {
  flex: 1;
  padding: 30px;
  position: relative;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #444 transparent;
}

.settings-content::-webkit-scrollbar {
  width: 8px;
}
.settings-content::-webkit-scrollbar-thumb {
  background-color: #444;
  border-radius: 4px;
}

/* Close Button */
.close-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  background: none;
  color: var(--chat-title);
  /* color: #ccc; */
  font-size: 1.5rem;
  border: none;
  cursor: pointer;
  transition: color 0.2s;
}

.close-btn:hover {
  color: #b5acac;
}

/* Theme Section */
.theme-options {
  display: flex;
  gap: 10px;
  margin-bottom: 24px;
  flex-wrap: wrap;
}

.theme-btn {
  flex: 1 1 30%;
  padding: 10px;
  border: 1px solid #444;
  border-radius: 6px;
  background: var(--setting-btn);
  /* background: #222; */
  color: var(--setting-li);
  /* color: #ccc; */
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  cursor: pointer;
  transition: background 0.2s, border-color 0.2s, color 0.2s;
  font-size: 0.95rem;
}

.theme-btn.selected {
  background: var(--search-btn);
  /* background: #444; */
  color: var(--theme-btn);
  /* color: #fff; */
  border-color: #666;
}

.theme-btn:hover {
  /* background: #333; */
  background: var(--search-btn);
  /* background: #333; */
  color: var(--theme-btn);
  /* color: #eee; */
}

/* Danger Zone Button */
.danger-btn {
  background-color: #a00000;
  color: white;
  padding: 10px 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  font-size: 0.95rem;
  transition: background 0.2s;
}

.danger-btn:hover {
  background-color: #c00000;
}

/* User Info */
.user-info {
  margin-bottom: 40px;
}

.user-info h3 {
  margin: 0 0 6px 0;
  font-size: 1.25rem;
  font-weight: 500;
}

.user-info p {
  margin: 4px 0;
  color: var(--chat-title);
  font-size: 0.95rem;
}

/* Coming Soon Placeholder */
.coming-soon {
  font-style: italic;
  color: #aaa;
  font-size: 0.9rem;
}

/* Danger Zone Row */
.danger-zone-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--setting-sidebar-link);
  /* background: #2c2c2c; */
  padding: 12px 16px;
  border-radius: 8px;
  margin-top: 20px;
  flex-wrap: wrap;
  gap: 10px;
}

.danger-zone-row h3 {
  margin: 0;
  font-size: 1.1rem;
  color: #f88;
}

/* RESPONSIVE STYLES */
@media (max-width: 768px) {
  .settings-modal {
    flex-direction: column;
    height: 95vh;
    width: 100%;
    border-radius: 10px;
  }

  .settings-sidebar {
    width: 100%;
    border-right: none;
    border-bottom: 1px solid #333;
    padding: 16px;
  }

  .settings-sidebar ul {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  .settings-sidebar li {
    flex: 1 1 45%;
    justify-content: center;
  }

  .settings-content {
    padding: 20px;
  }

  .theme-options {
    flex-direction: column;
  }

  .theme-btn {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .settings-content {
    padding: 16px;
  }

  .close-btn {
    top: 10px;
    right: 10px;
    font-size: 1.25rem;
  }

  .danger-zone-row {
    flex-direction: column;
    align-items: flex-start;
  }

  .danger-zone-row h3 {
    margin-bottom: 8px;
  }
}
