   .sidebar {
  width: 300px;
  background: var(--sidebar);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: width 0.3s ease-in-out;
  overflow: hidden;
  height: 100vh;
}
  
  .sidebar.collapsed {
    width: 50px;
    padding: 10px;
  }


  /* Sidebar Header (Hamburger & New Chat Button Row) */
.sidebar-header {
    display: flex;
    align-items: center;
    justify-content: space-between; /* Align left and right */
    width: 100%;
    margin-bottom: 15px;
}

/* Hamburger Button */
.toggle-btn {
    background: none;
    border: none;
    font-size: 24px;
    color: var(--toggle-btn);
    cursor: pointer;
    margin-bottom: 20px;
}

.new-chat-btn:hover {
    background: #555;
}

  
  /* Hide text when sidebar is collapsed */
  .sidebar.collapsed .profile,
  .sidebar.collapsed .new-chat-btn,
  .sidebar.collapsed .chat-history {
    display: none;
  }
  
