/* Smoothly show and hide the delete button */
.delete-btn {
  background: none;
  border: none;
  cursor: pointer;
  opacity: 0;  /* Start hidden */
  transform: translateX(10px); /* Move slightly right */
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.chat-item:hover .delete-btn {
  opacity: 1;  /* Fade in */
  transform: translateX(0); /* Move back */
}

.trash-icon {
  font-size: 18px;
  color: white;
  transition: color 0.3s ease;
}

.trash-icon:hover {
  color: red; /* Change color on hover */
}


  
/* Keep scrollbar at the right edge */
.chatbox {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    justify-content: space-between;
    background: var(--chatbox);
    /* background: #1B1C1D; */
    padding: 30px 0 40px 0;
    overflow-y: auto;
}
  
  /* Messages */

  .messages {
    flex-grow: 1;
    overflow-y: auto;
    padding-bottom: 20px;
    display: flex;
    /* width: 85%; */
    margin-top: 2%;
    padding-left: 8%;
    padding-right: 8%;
    flex-direction: column;
    /* align-self: stretch; Stretch to full width */
    scroll-behavior: smooth;
}

    /* Custom Scrollbar */
::-webkit-scrollbar {
    width: 8px; /* Adjust width */
}

::-webkit-scrollbar-thumb {
    background: #555; /* Darker color for better contrast */
    border-radius: 10px; /* Rounded edges */
    transition: background 0.3s ease-in-out;
}

::-webkit-scrollbar-thumb:hover {
    background: #777; /* Slightly lighter on hover */
}

::-webkit-scrollbar-track {
    background: #222; /* Dark background */
    border-radius: 10px;
}

/* For chatbox specific scrollbar */
.chatbox::-webkit-scrollbar {
    width: 6px;
}

.chatbox::-webkit-scrollbar-thumb {
    background: #555;
    border-radius: 10px;
}

.chatbox::-webkit-scrollbar-track {
    background: #2c2c2e;
}

  
