.sidebar-ad {
  position: absolute;
  bottom: 0;
  width: 20%;
  height: 25%;
  background: #1e1f22;
  padding: 10px;
  text-align: center;
  z-index: 2;
  border-top: 1px solid #333;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.sidebar-ad img {
  width: 100%;
  border-radius: 5px;
  transition: transform 0.3s ease;
}

.sidebar-ad img:hover {
  transform: scale(1.05);
}

.close-ad {
  background: none;
  border: none;
  color: rgb(0, 0, 0);
  font-size: 36px;
  cursor: pointer;
  position: absolute;
  top: 5px;
  z-index: 1;
  right: 5px;
}

.close-ad:hover {
  color: red;
}


  
/* üîç Search Popup (Like ChatGPT) */
.search-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(5px);
  z-index: 999;
}

/* Popup Box */
.search-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--share-button);
  /* background: #343541; */
  padding: 20px;
  width: 400px;
  border-radius: 10px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  animation: fadeIn 0.2s ease-in-out;
}

/* Popup Header */
.search-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
  border-bottom: 1px solid #4e4f52;
}

.search-header h2 {
  font-size: 18px;
  margin: 0;
  color: var(--new-chat-btn-title);
  /* color: white; */
}

.close-btn {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: var(--new-chat-btn-title);
  /* color: #ffffff; */
}

.close-btn:hover {
  color: #f25b5b;
}

/* Search Box */
.search-box {
  margin-top: 10px;
}

.search-box input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  background: var(--message-user);
  /* background: #40414f; */
  border: 1px solid #4e4f52;
  border-radius: 5px;
  outline: none;
  color: var(--share-button-title);
  /* color: white; */
}

.search-box input::placeholder {
  color: #ffffff;
  /* color: var(--share-button-hover); */
  /* color: #b4b4b4; */
}

/* Search Results */
.search-results {
  list-style: none;
  padding: 0;
  margin-top: 10px;
  max-height: 200px;
  overflow-y: auto;
}

.search-results li {
  padding: 10px;
  border-bottom: 1px solid #4e4f52;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
  color: var(--share-button-title);
  /* color: white; */
}

.search-results li:hover {
  background: var(--message-user);
  /* background: #2a2b33; */
}

.search-results li:active {
  background: #19c37d;
  color: black;
}

.no-result {
  text-align: center;
  color: gray;
  padding: 8px;
}

/* Fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate(-50%, -55%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}


.dev-mode-banner {
  background-color: #fff3cd;
  color: #856404;
  padding: 10px 20px;
  border-radius: 5px;
  border: 1px solid #ffeeba;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  width: 90%;
  max-width: 500px;
  box-sizing: border-box;
  font-size: 14px;
}

.dev-mode-banner button {
  position: absolute;
  top: 5px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 16px;
  cursor: pointer;
  color: #856404;
}

@media (max-width: 480px) {
  .dev-mode-banner {
    padding: 8px 12px;
    font-size: 13px;
  }

  .dev-mode-banner button {
    font-size: 18px;
    top: 25px;
    right: 8px;
  }
}



/* Responsive Styles */
/* üìå Mobile & Tablet (Max 1024px) */
@media (max-width: 1024px) {
  
    .chat-container{
      overflow: hidden;
    }
    /* Chat container: Stack items vertically */
    .chat-container {
        flex-direction: column;
        height: 100vh;
        width: 100vw;
    }

    /* Sidebar (Hidden by Default on Mobile) */
    .sidebar {
        position: fixed;
        left: -250px; /* Hide sidebar */
        width: 250px;
        height: 100vh;
        transition: left 0.3s ease-in-out;
        z-index: 1000;
        background: var(--sidebar);
        /* background: #202123; */
        display: flex;
        flex-direction: column;
        padding: 15px;
    }

    /* Sidebar Open State */
    .sidebar.open {
        left: 0; /* Slide sidebar into view */
    }

    /* Toggle Button Position */
    .toggle-btn {
        position: fixed;
        left: 10px;
        top: 10px;
        z-index: 1100;
        background: none;
        border: none;
        font-size: 34px;
        border-radius: 20px;
        color: var(--toggle-btn);
        cursor: pointer;
    }

    /* Sidebar Header */
    .sidebar-header {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }


      /* Profile Icon */
    .profile-icon {
      width: 40px;
      height: 40px;
      background-color: var(--share-button);;
      /* background-color: #333; */
      color: white;
      display: flex;
      align-items: center;
      margin-right: 25px;
      margin-top: 0px;
      padding: 5px;
      justify-content: center;
      border-radius: 50%;
      font-size: 35px;
      cursor: pointer;
    }

    /* Chatbox (Takes Full Width when Sidebar is Closed) */
    .chatbox {
        width: 100%;
        padding: 15px;
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background: #1B1C1D;
    }

    /* Messages */
    .messages {
        padding: 10px;
        flex-grow: 1;
        overflow-y: auto;
        display: flex;
        flex-direction: column;
        padding-left: 5%;
        padding-right: 5%;
        margin-top: 15%;
    }

    /* Chat Input Area */
    .input-area {
        width: 90%;
        margin: 0 auto;
        display: flex;
        padding: 8px;
        border-radius: 10px;
        background: var(--sidebar);
        /* background: #303030; */
        border: 1px solid #ffffff;
    }

    /* Chat Textarea */
    .input-area textarea {
        width: 100%;
        font-size: 16px;
        padding: 8px;
        height: 35px;
    }

    /* Scroll to Bottom Button */
    .scroll-bottom-btn {
        bottom: 100px;
    }
    .headerButton{
      display: flex;
      gap: 10px;
      align-items: center;
      margin-top: 20%;
    }
}

/* üìå Extra Small Screens (Max 600px) */
@media (max-width: 600px) {
    /* Sidebar */
    .sidebar {
        width: 220px;
    }

    /* Chat Messages */
    .messages {
        padding-left: 3%;
        padding-right: 3%;
    }

    /* Chatbox */
    .chatbox {
        padding: 10px;
        padding-top: 100px !important;
    }

    /* Chat Input */
    .input-area {
        width: 85%;
        align-self: center;
    }

    .new-chat-btn {
        width: 20%;
        margin-left: 100px;
        padding: 5px;
        font-size: 25px;
        margin-bottom: 20px;

        /* background: #444;
        color: rgb(221, 216, 216); */
        border: none;
        cursor: pointer;
        border-radius: 5px;
      }

      .search-btn {
        width: 20%;
        /* margin-left: -55px; */
        padding: 5px;
        font-size: 25px;
        margin-bottom: 20px;
        background: var(--search-btn);;
        /* background: #444; */
        color: rgb(221, 216, 216);
        border: none;
        cursor: pointer;
        border-radius: 5px;
      }


      
  .sidebar-ad {
    position: absolute;
    bottom: 0;
    width: 110%;
    background: #1e1f22;
    padding: 10px;
    text-align: center;
    z-index: 2;
    border-top: 1px solid #333;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  
  .sidebar-ad img {
    width: 100%;
