/* Navbar wrapper */
/* Navbar container */
.navbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
  z-index: 10;
  background: transparent;
}

/* Logo section */
.navbar-left {
  display: flex;
  align-items: center;
}

.navbar-logo {
  height: 200px;
  width: auto;
  object-fit: contain;
  cursor: pointer;
}

/* Hamburger menu icon */
.navbar-menu {
  cursor: pointer;
  color: #002b7f;
}

/* Slide-in menu container (mobile) */
.navbar-links-container {
  position: fixed;
  top: 0;
  right: -50%;
  width: 25%;
  height: 100vh;
  background: #ffffff;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.1);
  transition: right 0.3s ease-in-out;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* When menu is open */
.navbar-links-container.open {
  right: 0;
}

/* Nav links in mobile menu */
.navbar-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Individual link */
.navbar-links li a {
  text-decoration: none;
  color: #002b7f;
  font-weight: 600;
  font-size: 18px;
}

/* Close icon */
.close-icon {
  cursor: pointer;
  color: #002b7f;
}

/* Canada image inside heading */
.canada-logo {
  height: 1em;
  vertical-align: middle;
  margin-right: 6px;
}
/* Desktop links */
.navbar-desktop-links {
  display: none;
  list-style: none;
  gap: 32px;
  padding: 0;
  margin: 0;
}

.navbar-desktop-links li a {
  text-decoration: none;
  color: #002b7f;
  font-weight: 600;
  font-size: 18px;
}

