  }

  .navbar-logo {
    height: 200px;
    vertical-align: middle;
  }

  .hero2 h1 {
    font-size: 24px;
  }

  .subtext {
    font-size: 16px;
  }

  .primary-btn {
    padding: 10px 20px;
    font-size: 13px;
  }

  .features-row {
    flex-direction: column;
    align-items: center;
    font-size: 25px;
  }

  .hero2 h1{
    font-size: 30px;
  }

  .subtext{
    font-size: 20px;
  }
  .primary-btn{
    font-size: 20px;
  }
  /* .features-column{
    justify-self: center;
  } */

  .grid-4 {
    grid-template-columns: 1fr;
  }

