/* Small Devices (phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .grid-4 {
    grid-template-columns: 1fr;
  }

  .why-love {
    padding: 40px 20px;
  }

  .features-row {
    flex-direction: column;
    align-items: center;
  }
  /* .features-column{
    justify-items: center;
  } */

  /* .navbar-logo {
    height: 200px;
    vertical-align: middle;
  } */

  .navbar-desktop-links {
    display: flex;
    align-items: center;
  }

  .navbar-menu {
    display: none;
  }

    .navbar-links-container {
    width: 70%; /* More space on small devices */
  }

  .navbar-logo {
    height: 150px;
  }

  
  .navbar-links.desktop-only li a {
    text-decoration: none;
    color: #002b7f;
    font-weight: 600;
    font-size: 18px;
  }

  .navbar-links.desktop-only {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .hero2 h1 {
    font-size: 26px;
  }

  .subtext {
    font-size: 18px;
  }
}


/* Desktop view: show desktop links, hide hamburger */
@media screen and (min-width: 768px) {
  .navbar-desktop-links {
    display: flex;
    align-items: center;
    gap: 32px;
  }

  .navbar-menu {
    display: none;
  }
}

/* Medium Devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .navbar {
    padding: 0 30px;
  }

  .hero2 h1 {
    font-size: 28px;
  }

  .subtext {
    font-size: 18px;
  }

  .grid-4 {
    grid-template-columns: 1fr 1fr;
  }

  .why-love {
    padding: 50px 40px;
  }
}

/* Large Devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .navbar {
    padding: 0 40px;
  }

  .grid-4 {
    grid-template-columns: 1fr 1fr;
  }

  .why-love {
    padding: 60px 50px;
  }
}

/* Extra Large Devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .grid-4 {
    grid-template-columns: 1fr 1fr;
  }

  .why-love {
    padding: 60px 60px;
  }
}
