/* Floating button container fixed bottom-right */
.keyboard-shortcuts-popup {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Floating button that opens popup */
.shortcuts-button {
  background-color: #1f2937; /* dark gray */
  border: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  color: white;
  font-size: 28px;
  cursor: pointer;
  box-shadow: 0 4px 8px rgb(0 0 0 / 0.2);
  transition: background-color 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.shortcuts-button:hover {
  background-color: #3b82f6; /* blue */
}

/* Popup modal window */
.shortcuts-modal {
  position: fixed;
  bottom: 80px;
  right: 20px;
  width: 320px;
  max-height: 400px;
  background-color: #111827; /* dark background */
  color: white;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgb(0 0 0 / 0.5);
  padding: 20px 24px 24px 24px;
  overflow-y: auto;
  font-size: 14px;
  line-height: 1.5;
  user-select: text;
  z-index: 1100;
}

/* Modal content wrapper */
.modal-content {
  position: relative;
}

/* Header */
.shortcuts-modal h2 {
  margin-top: 0;
  font-size: 18px;
  font-weight: 600;
  border-bottom: 1px solid #374151; /* subtle border */
  padding-bottom: 8px;
}

/* List styles */
.shortcuts-modal ul {
  list-style: none;
  padding: 0;
  margin: 10px 0 0 0;
}

.shortcuts-modal li {
  margin-bottom: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Shortcut key styling */
.shortcuts-modal .key {
  background-color: #374151; /* darker gray */
  border-radius: 6px;
  padding: 6px 12px;
  font-family: monospace;
  font-weight: 700;
  color: #d1d5db; /* light gray */
  box-shadow: inset 0 -1px 0 rgb(0 0 0 / 0.2);
  user-select: none;
}

/* Close button */
.shortcuts-close-btn {
  position: absolute;
  top: 0px;
  /* top: 12px; */
  right: 12px;
  background: none;
  border: none;
  color: #9ca3af;
  cursor: pointer;
  font-size: 24px;
  font-weight: 700;
  line-height: 1;
  transition: color 0.2s ease;
  padding: 0;
}

.shortcuts-close-btn:hover {
  color: white;
}
